
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import axios from 'axios';
import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
export default {
name: "create",
data(){
return {
title:'',
content:'',

toolbars:{
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
fullscreen: true, // 
htmlcode: true, // html
help: true, // 
save: true, // eventssave
/* 1.4.2 */
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
preview: true, // 
}
};
},
methods:{

uploadImg:function(pos,file){
let _this = this;



let params = new FormData();
params.append('smfile',file);
axios.post('https://sm.ms/api/upload',params,{


'headers': { 'Content-Type': 'multipart/form-data' }
}).then(function (response) {

_this.$refs.md.$img2Url(pos,response.data.data.url);
});

},

addtopic:function () {

let _this=this;

this.$http.post('/topics',{
title:this.title,
content:this.content,
tab:'dev',
accesstoken:this.$store.state.accesstoken
}).then(function (response) {

if (response.data && response.data.success){

_this.$router.replace('/topiclist/dev')
}
})
}
},
components:{
Group,
XTextarea,
XButton,
mavonEditor
}
}



