
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
export default {
name: "edit",
props:['id'],
components:{
Group,
XTextarea,
XButton,
mavonEditor
},
data:function () {
return{
topic:{
title:'',
content:''
},
toolbars:{
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
fullscreen: true, // 
htmlcode: true, // html
help: true, // 
save: true, // eventssave
/* 1.4.2 */
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
preview: true, // 
}
};
},
methods:{
edittopic:function () {
let _this = this;
this.$http.post('/topics/update',{
topic_id:this.topic.id,
title:this.topic.title,
content:this.topic.content,
accesstoken:this.$store.state.accesstoken,
tab:'dev'
}).then(function(response){


_this.$router.back();
});
}
},
beforeRouteEnter:function (to,from,next) {
next(function (vm) {
vm.$http.get('/topic/'+to.params.id).then(function (response) {
vm.topic = response.data.data;
})
})
},
beforeRouteUpdate:function (to,from,next) {
let _this = this;
_this.$http.get('/topic/'+to.params.id).then(function (response) {
_this.topic = response.data.data;
next();
})
}
}



